import React from 'react'
import { HashRouter, Link, } from 'react-router-dom'


export default function Navigation({ history, }) {
    return (
        <div style={{
            padding: '1rem',
            boxShadow: '0 3px 4px #d9d9d9',
            display: 'flex',
            flexWrap: 'wrap',
        }}>
            <HashRouter history={history}>
                <Link to="/">
                    <button>Home</button>
                </Link>
                <Link to="/about">
                    <button>About</button>
                </Link>
            </HashRouter>
        </div>
    )
}
